/**
 *
 * Attributs:
 * Config init att: toolbars-prefix
 * Config att: last-datas : key des lastDatas pour l'init et le build
 */
export interface Workbench<C> extends BaseElement {
	initialize(init?: OWorkbenchInit<C>): this;
}

export interface OWorkbenchInit<C = {}> extends OSkinableInit, OLastDatasInit {
	ctx?: C;

	/**
	 * PrÃ©fixe des listes d'areas constituant les toolbars.
	 * Exemple : "myApp:" dÃ©finit les listes "myApp:tl", "myApp:tr", "myApp:lt", "myApp:lt", "myApp:rt", "myApp:rb", "myApp:bl", "myApp:br"
	 * DOIT Ãªtre spÃ©cifiÃ©.
	 */
	toolbarsPrefix?: string

	lt?: OWbToolsInit<C>
	lb?: OWbToolsInit<C>
	bl?: OWbToolsInit<C>
	br?: OWbToolsInit<C>
	rt?: OWbToolsInit<C>
	rb?: OWbToolsInit<C>
}

export interface IWbToolArea<C = {}, E extends HTMLElement = HTMLElement> extends IArea<C, E> {

	/** Interdit d'insÃ©rer cet Area dans un volet orientÃ© horizontalement ou verticalement. */
	forbidInLayout?: 'h' | 'v'
}

/** Wrap d'un IWbToolArea avec des indications sur le context graphique. */
export interface IWbToolLocatedArea<C = {}, E extends HTMLElement = HTMLElement> extends IWbToolArea<C, E> {
	/** area wrappÃ©e non localisÃ©e. */
	unlocatedArea: IWbToolArea<C, E>
	layout: 'h' | 'v'
}

/**
 * Vocabulaire :
 * - flap : volets gauche, droit et bas
 * - pan : panneau, zone d'affichage du contenu des views
 * - tabs : jeu d'onglets pour sÃ©lectionner le panneau avec le bouton de rÃ©duction de ces tabs et/ou de ces tabs.
 * - pans : regroupement du pan et de son tabs (pour le flap bas)
 */
export class Workbench<C> extends BaseElement implements IViewsContainer, ILastDatasBuilder {

	get tools(): NodeListOf<WbTools<C>> {return this.shadowRoot.querySelectorAll('c-wb-tools')}

	getAreaTools(): IArea<C>[] {
		const r = [] as IArea<C>[];
		for (const tool of this.tools) r.push(...tool.areas);
		return r;
	}

	showAreaTool(areaId: string): Promise<boolean> | void {
		for (const tool of this.tools) {
			const tab = tool.getTabByCode(areaId);
			if (tab) return tool.selectTab(tab);
		}
	}

	toggleAreaTool(areaId: string): Promise<boolean> | void {
		for (const tool of this.tools) {
			const tab = tool.getTabByCode(areaId);
			if (tab) return tab.selected ? tool.unselectTab() : tool.selectTab(tab);
		}
	}


	getTab(areaId: string): Tab<C> | null {
		for (const tool of this.tools) {
			const tab = tool.getTabByCode(areaId);
			if (tab) return tab;
		}
		return null;
	}

	protected _initialize(init: OWorkbenchInit<C>) {
		const reg = this.findReg(init);
		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		reg.installSkin('webzone:panel', sr);
		this._initAndInstallSkin(this.localName, init);

		const areasContext = init.ctx ? init.ctx : this;
		const tbPrefix = init.toolbarsPrefix;
		const lastDatas = init.lastDatas as JLDWorkbench;

		const ltT: IWbToolArea[] = reg.getList(tbPrefix + 'lt') || [];
		const lbT: IWbToolArea[] = reg.getList(tbPrefix + 'lb') || [];
		const blT: IWbToolArea[] = reg.getList(tbPrefix + 'bl') || [];
		const brT: IWbToolArea[] = reg.getList(tbPrefix + 'br') || [];
		const rtT: IWbToolArea[] = reg.getList(tbPrefix + 'rt') || [];
		const rbT: IWbToolArea[] = reg.getList(tbPrefix + 'rb') || [];

		const all = [ltT, lbT, blT, brT, rtT, rbT];

		//Replacement des tools par les userPrefs
		for (let k = 0, s=all.length; k<s; k++) {
			const l = all[k];
			for (let i = l.length - 1; i >= 0; i--) {
				const a = l[i];
				if (a) {
					const pos = reg.getUserData(tbPrefix + a.getId());
					if(pos && pos !== allPos[k]) {
						//On dÃ©place a vers pos
						switch(pos){
						case 'lt': ltT.push(a); break;
						case 'lb': lbT.push(a); break;
						case 'bl': blT.push(a); break;
						case 'br': brT.push(a); break;
						case 'rt': rtT.push(a); break;
						case 'rb': rbT.push(a); break;
						default: continue; //pos pas trouvÃ©, on passe.
						}
						l.splice(i, 1);
					}
				}
			}
		}

		if (lastDatas) {
			//Replacement des tools par les lastDatas
			function removeAndGet(area: string, layout: 'h' | 'v'): IWbToolArea | null {
				for (const l of all) {
					for (let i = l.length - 1; i >= 0; i--) {
						const a = l[i];
						if (a?.getId() === area && a.forbidInLayout !== layout) {
							//Area trouvÃ© et target layout compatible.
							l.splice(i, 1);
							return a;
						}
					}
				}
				return null;
			}

			function moveTo(areas: string[], target: IWbToolArea[], layout: 'h' | 'v') {
				if (!areas) return;
				for (const aId of areas) {
					const a = removeAndGet(aId, layout);
					if (a) target.push(a);
				}
			}

			moveTo(lastDatas.ltT, ltT, 'v');
			moveTo(lastDatas.lbT, lbT, 'v');
			moveTo(lastDatas.rtT, rtT, 'v');
			moveTo(lastDatas.rbT, rbT, 'v');
			moveTo(lastDatas.blT, blT, 'h');
			moveTo(lastDatas.brT, brT, 'h');
		}

		sr.appendChild(
			<div id="mid" c-resizable="">
				<div id="flapL" c-resizable="">
					<did id="pansLT" class="pans" c-resizable="">
						<div id="tabsLT" class="tabs">
							<button id="closeLT" title="Refermer ce volet" onclick={this.onClosePan}/>
						</div>
						<WbTools Ã®={Object.assign({orient: 'lt', areas: ltT, areasContext, lastDatas, workbench: this}, init.lt)}/>
					</did>
					<Resizer class="pansSep" c-orient="column"/>
					<did id="pansLB" class="pans" c-resizable="">
						<div id="tabsLB" class="tabs">
							<button id="closeLB" title="Refermer ce volet" onclick={this.onClosePan}/>
						</div>
						<WbTools Ã®={Object.assign({orient: 'lb', areas: lbT, areasContext, lastDatas, workbench: this}, init.lb)}/>
					</did>
					<div id="bTabsL" class="bTabs"/>
				</div>
				<Resizer c-orient="row"/>
				<div id="main" c-resizable="">
					<slot/>
				</div>
				<Resizer c-orient="row"/>
				<div id="flapR" c-resizable="">
					<did id="pansRT" class="pans pansH" c-resizable="">
						<div id="tabsRT" class="tabs">
							<button id="closeRT" title="Refermer ce volet" onclick={this.onClosePan}/>
						</div>
						<WbTools Ã®={Object.assign({orient: 'rt', areas: rtT, areasContext, lastDatas, workbench: this}, init.rt)}/>
					</did>
					<Resizer class="pansSep" c-orient="column"/>
					<did id="pansRB" class="pans pansH" c-resizable="">
						<div id="tabsRB" class="tabs">
							<button id="closeRB" title="Refermer ce volet" onclick={this.onClosePan}/>
						</div>
						<WbTools Ã®={Object.assign({orient: 'rb', areas: rbT, areasContext, lastDatas, workbench: this}, init.rb)}/>
					</did>
					<div id="bTabsR" class="bTabs"/>
				</div>
			</div>
		);

		sr.appendChild(<Resizer class="sep" c-orient="column"/>);
		sr.appendChild(<div id="flapB" c-resizable="">
			<div id="pansBL" class="pans" c-resizable="">
				<div id="tabsBL" class="tabs">
					<button id="closeBL" title="Refermer ce volet" onclick={this.onClosePan}/>
				</div>
				<WbTools Ã®={Object.assign({orient: 'bl', areas: blT, areasContext, lastDatas, workbench: this}, init.bl)}/>
			</div>
			<Resizer class="pansSep" c-orient="row"/>
			<div id="pansBR" class="pans" c-resizable="">
				<div id="tabsBR" class="tabs">
					<button id="closeBR" title="Refermer ce volet" onclick={this.onClosePan}/>
				</div>
				<WbTools Ã®={Object.assign({orient: 'br', areas: brT, areasContext, lastDatas, workbench: this}, init.br)}/>
			</div>
		</div>);

		//LastDatas
		if (init.lastDatasKey) this.setAttribute("last-datas", init.lastDatasKey);
		if (lastDatas) {
			if (lastDatas.mw > 0) sr.getElementById("main").style.flexBasis = lastDatas.mw + "px";
			if (lastDatas.mh > 0) sr.getElementById("mid").style.flexBasis = lastDatas.mh + "px";
			if (lastDatas.lw > 0) sr.getElementById("flapL").style.flexBasis = lastDatas.lw + "px";
			if (lastDatas.rw > 0) sr.getElementById("flapR").style.flexBasis = lastDatas.rw + "px";
			if (lastDatas.bh > 0) sr.getElementById("flapB").style.flexBasis = lastDatas.bh + "px";
			if (lastDatas.lth) sr.getElementById("pansLT").style.flexBasis = lastDatas.lth;
			if (lastDatas.lbh) sr.getElementById("pansLB").style.flexBasis = lastDatas.lbh;
			if (lastDatas.blw) sr.getElementById("pansBL").style.flexBasis = lastDatas.blw;
			if (lastDatas.brw) sr.getElementById("pansBR").style.flexBasis = lastDatas.brw;
			if (lastDatas.rth) sr.getElementById("pansRT").style.flexBasis = lastDatas.rth;
			if (lastDatas.rbh) sr.getElementById("pansRB").style.flexBasis = lastDatas.rbh;
		}
		for (const tools of this.tools) tools.injectTabs();
		for (const tools of this.tools) tools.refreshSelectedTab();
	}

	buildInitFromAtts(init?: OWorkbenchInit<C>): OWorkbenchInit<C> {
		init = super.buildInitFromAtts(init);
		init.toolbarsPrefix = this.getAttribute("toolbars-prefix");
		init.lastDatas = LASTDATAS.getLastDatas(this, this.getAttribute("last-datas"));
		return init;
	}

	buildLastDatas(p: JLastDatas) {
		const k = this.getAttribute("last-datas");
		if (k) {
			const d = p[k] = {} as JLDWorkbench;
			const sr = this.shadowRoot;
			d.mw = sr.getElementById("main").offsetWidth;
			d.mh = sr.getElementById("mid").offsetHeight;
			d.lw = sr.getElementById("flapL").offsetWidth;
			d.rw = sr.getElementById("flapR").offsetWidth;
			d.bh = sr.getElementById("flapB").offsetHeight;
			d.lth = sr.getElementById("pansLT").style.flexBasis;
			d.lbh = sr.getElementById("pansLB").style.flexBasis;
			d.blw = sr.getElementById("pansBL").style.flexBasis;
			d.brw = sr.getElementById("pansBR").style.flexBasis;
			d.rth = sr.getElementById("pansRT").style.flexBasis;
			d.rbh = sr.getElementById("pansRB").style.flexBasis;
			for (const tools of this.tools) {
				if (tools.forcedTools?.length > 0) (d as any)[tools.id + "T"] = tools.forcedTools;
				tools.buildLastDatas(d);
				//else d[tools.id as 'lt' | 'lb' | 'rt' | 'rb' | 'bl' | 'br'] = null
			}
		}
		//on poursuit dans le contenu principal
		LASTDATAS.buildLastDatas(p, this);
	}

	visitViews(visitor: (view: IView) => any, options?: OViewVisitOptions) {
		for (const tools of this.tools) {
			if (!options || !options.visible || !tools.closest('[hidden]')) {
				const r = tools.visitViews(visitor, options);
				if (r !== undefined) return r;
			}
		}
	}

	async visitViewsAsync(visitor: (view: IView) => Promise<any>, options?: OViewVisitOptions): Promise<any> {
		for (const tools of this.tools) {
			if (!options || !options.visible || !tools.closest('[hidden]')) {
				const r = await tools.visitViewsAsync(visitor, options);
				if (r !== undefined) return r;
			}
		}
	}

	findTabView(codeView: string): Tab {
		for (const tools of this.tools) {
			const tab = tools.getTabByCode(codeView);
			if (tab) return tab;
		}
		return null;
	}

	async showView(codeView: string): Promise<IView> {
		const tab = this.findTabView(codeView);
		if (tab && await tab.parentViewCtn.selectTab(tab)) return tab.view;
		return null;
	}

	onClosePan(this: Button) {
		(this.parentElement.nextElementSibling as WbTools<C>).unselectTab();
	}
}

interface JLDWorkbench {
	//Tailles
	mw?: number
	mh?: number
	lw?: number
	rw?: number
	th?: number
	bh?: number
	lth?: string
	lbh?: string
	blw?: string
	brw?: string
	rth?: string
	rbh?: string
	//DÃ©placements
	ltT?: string[]
	lbT?: string[]
	blT?: string[]
	brT?: string[]
	rtT?: string[]
	rbT?: string[]
	//SubLastDatas
	main?: JLastDatas
	lt?: JLastDatas
	lb?: JLastDatas
	rt?: JLastDatas
	rb?: JLastDatas
	bl?: JLastDatas
	br?: JLastDatas
}

