customElements.define('c-workbench', Workbench);

interface OWbToolsInit<C> extends OTabsInit<C> {
	orient?: 'lt' | 'lb' | 'rt' | 'rb' | 'bl' | 'br'
	vertical?: never
	lastDatas?: JLDWorkbench | JLDTabs //init via JLDWorkbench rÃ©solu en JLDTabs
	workbench?: Workbench<C>
}

class WbTools<C> extends Tabs<C> {

	forcedTools?: string[]

	/** Ce pan est-il vertical (lt, lb, rt, rb) ou horizontal (bl, br) ? */
	isVerticalPan: boolean

	/** Ce pan est-il le premier dans le flap (lt, rt, bk) */
	isFirstPan: boolean

	/** Ce pan est-il Ã  droite (rt, rb, br) */
	isRightPan: boolean

	tabsBar: HTMLDivElement;

	get flapClosed(): boolean {return this.classList.contains("flapClosed")}

	protected async _initialize(init: OWbToolsInit<C>): Promise<void> {
		init.noArrowScroll = true;
		this.id = init.orient;
		const firstPos = this.id[0];
		const secondPos = this.id[1];
		this.isVerticalPan = firstPos !== 'b';
		this.isFirstPan = secondPos === 't' || secondPos === 'l';
		this.isRightPan = this.isVerticalPan ? firstPos === 'r' : secondPos === 'r';

		this.classList.add(this.isVerticalPan ? "panV" : "panH");
		if (init.lastDatas) {
			this.forcedTools = (init.lastDatas as any)[init.orient + "T"] as string[];
			init.lastDatas = (init.lastDatas as JLDWorkbench)[init.orient] as JLDTabs;
		}
		this.setAttribute("last-datas", init.orient);
		await super._initialize(init);
		if (!this._areas) this._areas = [];
	}

	protected _initSkins(init: OTabsInit<C>) {
	}

	/** Finalise l'init avec le placement des tabs pour ce pan. */
	injectTabs() {
		this.tabsBar = this.previousElementSibling as HTMLDivElement;
		this.tabsBar.insertAdjacentElement("afterbegin", this._tabs);
	}

	_initShadowRoot() {
		this.shadowRoot.appendChild(this._mainSlot);
	}


	_initTabs() {
		this._areas?.forEach((area: IArea<C>) => {this._newTab(area)});
	}

	async selectTab(tab: Tab<C>, lastDatas?: JLastDatas): Promise<boolean> {
		if (tab == null) return this.unselectTab();
		const res = await super.selectTab(tab, lastDatas);
		this.refreshSelectedTab();
		return res;
	}

	async unselectTab(): Promise<boolean> {
		if (!this._selectedTab) return false;
		if (!await this._selectedTab.hide()) return false;
		this._selectedTab = null;
		this.refreshSelectedTab();
		return true;
	}

	async closeTab(tab: Tab<C>, silently?: boolean, removeFromAreas?: boolean): Promise<boolean> {
		const ok = await super.closeTab(tab, silently, removeFromAreas);
		if (ok) this.refreshSelectedTab();
		return ok;
	}

	async addTab(area: IArea<C>, options?: { select?: boolean; position?: number; view?: IView }): Promise<void> {
		await super.addTab(area, options);
		this.refreshSelectedTab();
	}

	_newTab(area: IArea<C>, before?: Tab<C>, view?: IView): Tab<C> {
		const tab = new WbToolTab<C>().initialize({
			parentViewCtn: this,
			area,
			view
		});
		tab.onclick = function (this: WbToolTab<C>) {
			this.parentViewCtn.selectTab(this);
		};
		tab.showOnDragOver = true;
		this._tabs.insertBefore(tab, before);
		return tab;
	}

	refreshSelectedTab() {
		if (DOM.setHidden(this.getPans(), !this.hasTabs())) {
			DOM.setHidden(this.getPanResizer(), !this.hasTabs())
		}

		const siblingTools = this.getSiblingTools();
		const flapClosed: boolean = !this._selectedTab && !siblingTools.selectedTab;
		const panCollapsed = !this._selectedTab;
		const panSiblingCollapsed = !siblingTools.selectedTab;

		this.getFlapResizer().disabled = flapClosed;
		this.setFlapClosed(flapClosed, panCollapsed, panSiblingCollapsed);
		siblingTools.setFlapClosed(flapClosed, panSiblingCollapsed, panCollapsed);
		this.getFlap().classList.toggle("closed", flapClosed);
		if (flapClosed) this.getFlap().style.flex = ""; //reset Resizer
		this.tabsBar.classList.toggle("collapsed", panCollapsed);
		if (DOM.setHidden(this, panCollapsed) && panCollapsed) this.getPans().style.flex = ""; //reset Resizer
		this.getPanResizer().disabled = panCollapsed || panSiblingCollapsed;

		if (!panCollapsed) this.getTabsCloseBtn().classList.toggle("sibling", !panSiblingCollapsed);
		if (!panSiblingCollapsed) siblingTools.getTabsCloseBtn().classList.toggle("sibling", !panCollapsed);

		this.refresh();
		siblingTools.refresh();
	}

	/** Ajustement du pan et de ses tabs en fonction de l'Ã©tat ouvert/fermÃ© de son flap */
	protected setFlapClosed(closed: boolean, panCollapsed: boolean, panSiblingCollapsed: boolean) {
		this.classList.toggle("flapClosed", closed);
		if (!this.isVerticalPan) {
			//flap bas, on place les tabs dans le bon container
			const tabs = this.tabsBar;
			if (closed) {
				if (tabs.parentElement === this.parentElement) this.getTabsBarClosed().appendChild(tabs);
			} else {
				if (tabs.parentElement !== this.parentElement) this.insertAdjacentElement("beforebegin", tabs);
			}
		}
		if (!this.isVerticalPan) {
			//Flap bas
			DOM.setAttrBool(this, 'vertical', panCollapsed && (this.isLateralFlapCollapsed() || !panSiblingCollapsed));
		} else {
			//Flap latÃ©ral,
			DOM.setAttrBool(this, 'vertical', closed);
			//on ajuste notre flap bas
			const bottomTools = this.getBottomTools();
			if (DOM.setAttrBool(bottomTools, 'vertical', !bottomTools.selectedTab && (panCollapsed && panSiblingCollapsed || !!bottomTools.getSiblingTools().selectedTab))) bottomTools.refresh();
		}

		if (closed) this.getPans().style.flex = ""; //reset Resizer
		DOM.setAttr(this.getPans(), "pan-state", closed ? "closed" : !this._selectedTab ? "collapsed" : "opened");
	}

	getPans(): HTMLElement {
		return this.parentElement;
	}

	getFlap(): HTMLElement {
		return this.parentElement.parentElement;
	}

	getFlapResizer(): Resizer {
		const flap = this.getFlap();
		if (flap.id === "flapL") return flap.nextElementSibling as Resizer;
		return flap.previousElementSibling as Resizer; //flapR et flapB
	}

	getPanResizer(): Resizer {
		//if (this.isVerticalPan) return (this.isFirstPan ? this.nextElementSibling : this.previousElementSibling.previousElementSibling) as Resizer;
		return (this.isFirstPan ? this.parentElement.nextElementSibling : this.parentElement.previousElementSibling) as Resizer;
	}

	getTabsCloseBtn(): HTMLButtonElement {
		return this.tabsBar.lastElementChild as HTMLButtonElement;
	}

	/** Onglets refermÃ©s des pans en bas */
	getTabsBarClosed(): HTMLDivElement {
		return DOMSH.findDocumentOrShadowRoot(this).getElementById(this.isRightPan ? "bTabsR" : "bTabsL") as HTMLDivElement;
	}

	getSiblingTools(): WbTools<C> {
		//if (this.isVerticalPan) return (this.isFirstPan ? this.nextElementSibling.nextElementSibling.nextElementSibling : this.previousElementSibling.previousElementSibling.previousElementSibling) as WbTools<C>;
		return (this.isFirstPan ? this.parentElement.nextElementSibling.nextElementSibling.lastElementChild : this.parentElement.previousElementSibling.previousElementSibling.lastElementChild) as WbTools<C>;
	}

	getBottomTools(): WbTools<C> {
		return DOMSH.findDocumentOrShadowRoot(this).getElementById(this.isRightPan ? "br" : "bl") as WbTools<C>;
	}

	isLateralFlapCollapsed() {
		return DOMSH.findDocumentOrShadowRoot(this).getElementById(this.isRightPan ? "flapR" : "flapL").classList.contains("closed");
	}

	onMoveTab(tab: WbToolTab<C>, ev: Event) {
		const me = this;

		async function onSelPan(this: HTMLButtonElement, ev: Event) {
			POPUP.findPopupableParent(this).close();
			const area = tab.area;
			const target = DOMSH.findDocumentOrShadowRoot(me).getElementById(this.id) as WbTools<C>;
			await me.unselectTab();
			if (await me.closeTab(tab, false, true)) {
				const idx = me.forcedTools?.indexOf(area.getId());
				if (idx >= 0) me.forcedTools.splice(idx, 1);
				(target.forcedTools || (target.forcedTools = [])).push(area.getId());
				await target.addTab(area, {select: true});
			}
		}

		function makeBtn(id: string, layout: 'h' | 'v') {
			return <button id={id} onclick={onSelPan} class={me.id === id ? 'current' : undefined} disabled={me.id === id || (tab.area as IWbToolArea).forbidInLayout === layout ? "" : undefined} style={"grid-area:" + id}/>
		}

		const body = <div><ShadowJsx skin="c-wb-tools/movePopup">
			{makeBtn("lt", 'v')}
			{makeBtn("lb", 'v')}
			<div id="central">SÃ©lectionnez un des volets disponibles</div>
			{makeBtn("rt", 'v')}
			{makeBtn("rb", 'v')}
			{makeBtn("bl", 'h')}
			{makeBtn("br", 'h')}
		</ShadowJsx></div>;
		const isBottom = me.id.charAt(0) === 'b';
		POPUP.showFloating(body, {posFrom: ev.target as HTMLElement, fromY: isBottom ? "top" : "bottom", targetY: isBottom ? "bottom" : "top", toleranceX: 500}, this, {closeOnBlur: true});
	}

	_onClose(this: Button, ev: Event) {
		DOMSH.findHost<WbTools<C>>(this).unselectTab();
	}
}

