customElements.define('c-wb-tools', WbTools);

class WbToolTab<C> extends Tab<C> implements IContextMenuActionsPointer<WbToolTab<C>> {

	parentViewCtn: WbTools<C>;

	ctxMenuActions: IContextMenuActions<WbToolTab<C>> = {
		actions: [
			new Action("openClose").setLabel((ctx: WbToolTab<C>) => ctx._shown ? "Refermer ce volet" : "Ouvrir ce volet")
				.setExecute((ctx: WbToolTab<C>, ev) => {
					if (ctx._shown) ctx.parentViewCtn.unselectTab();
					else ctx.parentViewCtn.selectTab(ctx);
				}),
			new Action("move").setLabel("DÃ©placer ce volet...").setExecute((ctx: WbToolTab<C>, ev) => {
				ctx.parentViewCtn.onMoveTab(ctx, ev);
			})
		], actionContext: this
	};

	protected _initialize(init: OTabInit<C>) {
		//init config
		this.parentViewCtn = init.parentViewCtn as WbTools<C>;
		this.view = init.view;
		this.area = init.area;
		//init en dur de OButtonConfig
		this.setAttribute("role", "button"); //XXX aria role dÃ©diÃ© pour tab ?
		BASIS.makeClickable(this);
		//pas de shadowDom
		this._createIcon();
		this.ondblclick = (ev) => {
			if (ContextMenuDeskFeat.isIn(desk)) desk.showContextMenu.call(document.body, ev);
		}
	}

	async show(lastDatas?: JLastDatas): Promise<void> {
		const layout = (this.parentViewCtn as WbTools<any>).isVerticalPan ? "v" : "h";
		if ((this.area as IWbToolLocatedArea).layout !== layout) {
			//Wrap this.area avec les info de layout.
			const unlocated = (this.area as IWbToolLocatedArea).unlocatedArea || this.area;
			const located = Object.create(unlocated) as IWbToolLocatedArea;
			located.layout = layout;
			located.unlocatedArea = unlocated;
			this.area = located;
		}
		await super.show(lastDatas);
	}

	protected _refresh() {
		super._refresh();
		DOM.setAttr(this._iconElt, "title", VIEWS.getDescription(this.view || this) || VIEWS.getLabel(this.view || this)); //pour le mode collapsÃ©.
	}
}

customElements.define('c-wb-tooltab', WbToolTab);

